const canvas = document.getElementById("jsCanvas")
const body = document.querySelector("body")
const ctx = canvas.getContext("2d") // ÎìúÎ°úÏûâ context Ïóê Ï†ëÍ∑º
const colors = document.getElementsByClassName("jsColor")
const mode = document.getElementById("jsMode")
const range = document.getElementById("jsRange")
const saveBtn = document.getElementById("jsSave")

const INITIAL_COLOR = "#2c2c2c"

// js - canvas ÌÅ¨Í∏∞ Ï†ïÏùò
canvas.width = 450
canvas.height = 450

// ÏÑ† Ïä§ÌÉÄÏùº, ÎèôÏûë default Í∞í -----------------------------------------
ctx.fillStyle = "#fff"
ctx.fillRect(0, 0, canvas.width, canvas.height)
ctx.strokeStyle = INITIAL_COLOR
ctx.fillStyle = INITIAL_COLOR
ctx.lineWidth = 5;

let painting = false
let filling = false

// Function -----------------------------------------
function stopPainting() {
  painting = false
}

function startPainting() {
  painting = true
}

// ÏÑ† Í∑∏Î¶¨Í∏∞ (painting)
function onMouseMove(event) {
  let x = event.offsetX // ÎßàÏö∞Ïä§ Ï¢åÌëú Î≥ÄÏàòÌôî
  let y = event.offsetY
  if (!painting) { // ÏïÑÏßÅ ÏïàÍ∑∏Î¶¥ Îïå(false) ÏûëÎèô (Ï∫îÎ≤ÑÏä§Ïóê ÌÅ¥Î¶≠ ÏïàÎêú ÏÉÅÌÉú)
    ctx.beginPath() // Í≤ΩÎ°ú ÏÉùÏÑ±
    ctx.moveTo(x, y) // ÏãúÏûëÏ†ê Ï¢åÌëú ÏßÄÏ†ï
  } else {
    ctx.lineTo(x, y)
    ctx.stroke()
  }
}

// ÎßàÏö∞Ïä§Í∞Ä Ï∫îÎ≤ÑÏä§ Î∞ñÏúºÎ°ú Í∞îÎã§Í∞Ä ÏöîÏÜå ÏïàÏúºÎ°ú Îã§Ïãú Îì§Ïñ¥ÏôîÏùÑ Îïå ÏãúÏûëÏ†ê Ï¢åÌëú Ïû¨Ìï†Îãπ
function onMouseEnter(event) {
  x = event.offsetX
  y = event.offsetY
  ctx.moveTo(x, y)
}

// ÏÉâ ÏßÄÏ†ï (controls color)
function colorClickHandler(event) {
  const color = event.target.style.backgroundColor
  ctx.strokeStyle = color
  ctx.fillStyle = color
}

// toggle(paint / fill) Î≤ÑÌäº Ïù¥Î≤§Ìä∏ 
function modeClickHandler() {
  if (!filling) { // filling = false ÏÉÅÌÉúÏùº Îïå Ïã§Ìñâ (Ï¥àÍ∏∞Í∞í)
    filling = true
    mode.innerText = "Paint"
  } else { // filling = true ÏÉÅÌÉúÏùº Îïå Ïã§Ìñâ
    filling = false
    mode.innerText = "Fill"
  }
}

// canvas ÎàÑÎ•¥Î©¥ Ïã§Ìñâ (filling) 
function canvasClickHandler() {
  if (filling) { // filling ÌôúÏÑ±Ìôî (= true) ÏÉÅÌÉúÏùº Îïå Ïã§Ìñâ
    ctx.fillRect(0, 0, canvas.width, canvas.height)
  }
}

// ÏÑ† ÍµµÍ∏∞ Ï°∞Ï†ï input
function rangeChangeHandler(event) {
  const size = event.target.value
  ctx.lineWidth = size
}

// context menual (Ïö∞ÌÅ¥Î¶≠)Í∏∞Îä• ÎßâÍ∏∞
function CM_Handler(event) {
  event.preventDefault()
}

// canvas Ïùò Îç∞Ïù¥ÌÑ∞Î•º image Î°ú ÏñªÍ≥† download
function saveClickHandler() {
  const image = canvas.toDataURL()
  const link = document.createElement("a")
  link.href = image
  link.download = "PaintJS[üé®]"
  link.click()
}

// AddEventListener -----------------------------------------
if (canvas) {
  canvas.addEventListener("mousemove", onMouseMove)
  canvas.addEventListener("mousedown", startPainting)
  canvas.addEventListener("mouseenter", onMouseEnter)
  canvas.addEventListener("click", canvasClickHandler)
  canvas.addEventListener("contextmenu", CM_Handler)
}

// ÏÑ†ÏùÑ canvas Î∞îÍπ•ÍπåÏßÄ Í∑∏Î¶¨ ÏÉÅÌÉúÏóêÏÑú ÌÅ¥Î¶≠ÏùÑ ÎóÄ ÏÉÅÌÉúÏóê Ï∫îÎ≤ÑÏä§ ÏïàÏóê Îã§Ïãú Îì§Ïñ¥ÏôÄÎèÑ ÏÑ†Ïù¥ Í∑∏Î†§ÏßÄÎäî Ïò§Î•ò
// Ìï¥Í≤∞ : mouseup Ïù¥Î≤§Ìä∏Î•º canvas Ïóê Íµ≠ÌïúÏãúÌÇ§ÏßÄ ÏïäÍ≥† body Ï†ÑÏ≤¥Ïóê Ï†ÅÏö©
// ÌÅ¥Î¶≠ÏùÑ ÎñºÎ©¥ canvas ÎÇ¥ÏóêÏÑú ÎøêÎßåÏù¥ ÏïÑÎãå, ÏïÑÏòà painting Ïù¥ Î©àÏ∂§
body.addEventListener("mouseup", stopPainting) 

Array.from(colors).forEach(color => color.addEventListener("click", colorClickHandler)) // colors[] array ÏÉùÏÑ± >> Í∞Å item Ïóê ÌÅ¥Î¶≠ Ïù¥Î≤§Ìä∏ Ï≤òÎ¶¨

if (mode) { // toggle Î≤ÑÌäº Ïù¥Î≤§Ìä∏ >> FILL mode / PAINT mode 
  mode.addEventListener("click", modeClickHandler)
}

if (range) { // ÏÑ†ÍµµÍ∏∞ Ï°∞Ï†ï input Ïù¥Î≤§Ìä∏ 
  range.addEventListener("input", rangeChangeHandler)
}

if (saveBtn) { // ÌååÏùº Ï†ÄÏû• Ïù¥Î≤§Ìä∏
  saveBtn.addEventListener("click", saveClickHandler)
}